<template>
  <div>
    <div>
      上传 txt文件，到富文本中编辑
    </div>
    <el-upload
      class="upload-demo"
      accept=".txt"
      action=""
      :on-change="handleChange"
      :file-list="fileList"
      :auto-upload="false"
      :multiple="true"
    >
      <el-button style="margin:20px 0" size="small" type="primary"
        >点击上传</el-button
      >
    </el-upload>

    <quill-editor class="mt15" ref="myQuillEditor" v-model="content" :options="editorOption">
    </quill-editor>
  </div>
</template>

<script>
import { Quill } from 'vue-quill-editor'
import { lineHeightStyle } from '@/utils/lineHeight'
Quill.register({ 'formats/lineHeight': lineHeightStyle }, true)
const toolbarOptions = [
  ['bold', 'italic', 'underline', 'strike'],        // 加粗，斜体，下划线，删除线
  ['blockquote', 'code-block'],                     //引用，代码块
  [{ 'header': 1 }, { 'header': 2 }],               // 几级标题
  [{ 'list': 'ordered' }, { 'list': 'bullet' }],    // 有序列表，无序列表
  [{ 'script': 'sub' }, { 'script': 'super' }],     // 下角标，上角标
  [{ 'indent': '-1' }, { 'indent': '+1' }],         // 缩进
  [{ 'direction': 'rtl' }],                         // 文字输入方向
  [{ 'size': ['small', false, 'large', 'huge'] }],  // 字体大小
  [{ 'header': [1, 2, 3, 4, 5, 6, false] }],        // 标题
  [{ 'color': [] }, { 'background': [] }],          // 颜色选择
  [{ 'font': [] }], // 字体
  [{ 'align': [] }],    // 居中
  ['clean'],            // 清除样式,
  ['link', 'image'],  // 上传图片、上传视频
  [{ lineheight: ['initial', '1', '1.5', '1.75', '2', '3', '4', '5'] }]
]
export default {
  data() {
    return {
      content: "展示当前及以前累计天数的总和，如用户修改累计数展示当前及以前累计天数的总和，如用户修改累计数展示当前及以前累计天数的总和，如用户修改累计数展示当前及以前累计天数的总和，如用户修改累计数展示当前及以前累计天数的总和，如用户修改累计数展示当前及以前累计天数的总和，如用户修改累计数展示当前及以前累计天数的总和，如用户修改累计数展示当前及以前累计天数的总和，如用户修改累计数",
      editorOption: {
        placeholder: '请在这里输入',
        theme: 'snow',
        history: {
          delay: 1000,
          maxStack: 50,
          userOnly: false
        },
        modules: {
          toolbar: {
            container: toolbarOptions, //工具栏
            handlers: {
              lineheight: (value) => {
                if (value) {
                  let quill = this.$refs.myQuillEditor.quill;
                  quill.format("lineHeight", value)
                }
              }
            }
          },
        }
      },
      fileList: []
    };
  },
  methods: {
    // 改变文件
    handleChange(file, fileList) {
      let reader = new FileReader();
      if (typeof FileReader === "undefined") {
        this.$message({
          type: "info",
          message: "您的浏览器不支持文件读取。"
        });
        return;
      }
      reader.readAsArrayBuffer(file.raw); //读任意文件
      let _this = this;
      reader.onload = function(e) {
        let ints = new Uint8Array(e.target.result);
        let snippets = new TextDecoder("utf8").decode(ints);
        _this.content += snippets;
      };
    }
  },
  created() {}
};
</script>
<style lang="less">
.ql-container {
  // 设置默认字号
  font-size: 16px;
  height: 300px;
}
.ql-snow .ql-picker.ql-lineheight .ql-picker-label::before {
  content: '行高';
}
.ql-snow .ql-picker.ql-lineheight .ql-picker-item[data-value='1']::before {
  content: '1';
}
.ql-snow .ql-picker.ql-lineheight .ql-picker-item[data-value='1.5']::before {
  content: '1.5';
}
.ql-snow .ql-picker.ql-lineheight .ql-picker-item[data-value='1.75']::before {
  content: '1.75';
}
.ql-snow .ql-picker.ql-lineheight .ql-picker-item[data-value='2']::before {
  content: '2';
}
.ql-snow .ql-picker.ql-lineheight .ql-picker-item[data-value='3']::before {
  content: '3';
}
.ql-snow .ql-picker.ql-lineheight .ql-picker-item[data-value='4']::before {
  content: '4';
}
.ql-snow .ql-picker.ql-lineheight .ql-picker-item[data-value='5']::before {
  content: '5';
}
.ql-snow .ql-picker.ql-lineheight {
  width: 70px;
}

.ql-editor .ql-indent-1:not(.ql-direction-rtl) {
  padding-left: 0!important;
  text-indent: 1em;
}
.ql-editor .ql-indent-2:not(.ql-direction-rtl) {
  padding-left: 0!important;
  text-indent: 2em;
}
.ql-editor .ql-indent-3:not(.ql-direction-rtl) {
  padding-left: 0!important;
  text-indent: 3em;
}
.ql-editor .ql-indent-4:not(.ql-direction-rtl) {
  padding-left: 0!important;
  text-indent: 4em;;
}
.ql-editor .ql-indent-5:not(.ql-direction-rtl) {
  padding-left: 0!important;
  text-indent: 5em;
}
.ql-editor .ql-indent-6:not(.ql-direction-rtl) {
  padding-left: 0!important;
  text-indent: 6em;
}
.ql-editor .ql-indent-7:not(.ql-direction-rtl) {
  padding-left: 0!important;
  text-indent: 7em;
}
.ql-editor .ql-indent-8:not(.ql-direction-rtl) {
  padding-left: 0!important;
  text-indent: 8em;
}

</style>


